</template>

<script>
import $ from "jquery";
import Navbar from "./Navbar";
import Carousel from "./Carousel";
import Footer from "./Footer";
import SaleSwiper from "./Sale-swiper.vue";
import Alert from "../AlertMessage";
import Clipboard from 'clipboard';

// 
import aboutMeImg from "@/assets/img/info.jpg";
import tableImg from "@/assets/img/qp_01.jpg";
import bouquetImg from "@/assets/img/qp_02.jpg";
import weddingImg from "@/assets/img/qp_03.jpg";
import newArrivalImg from "@/assets/img/na_01.jpg";
import onSaleImg from "@/assets/img/os_01.jpg";

export default {
  name: "Home",
  components: {
    Navbar,
    Carousel,
    Footer,
    SaleSwiper,
    Alert
  },
  data() {
    return {
      newArrivalImg,
      tableImg,
      bouquetImg,
      weddingImg,
      onSaleImg,
      aboutMeImg
    };
  },
  methods: {
    doCopy(text){
      const vm = this;
      let cb = new Clipboard('.copy-btn');
      cb.on('success',() => {
        cb.destroy(); // 
        vm.$bus.$emit('message:push','' + text,'success');
      });
      cb.on('error',() => {
        cb.destroy(); // 
        vm.$bus.$emit('message:push','','warning');
      });
    }
  },
  mounted(){
     $('#Notification').modal('show');
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.flow {
