<template>
  <div>
    <div class="container">
      <Navbar></Navbar>
      <div class="banner row pt-5 pb-5">
        <h2 class="font-weight-bolder text-center w-100">FAQ</h2>
      </div>
      <div class="card my-5">
        <div class="card-header">
          <ul class="nav nav-tabs card-header-tabs">
            <li class="nav-item">
              <a
                class="nav-link"
                :class="{ active: whichIs === '' }"
                href="#"
                @click.prevent="pickQ('')"
                ></a
              >
            </li>
            <li class="nav-item">
              <a
                class="nav-link"
                :class="{ active: whichIs === '' }"
                href="#"
                @click.prevent="pickQ('')"
                ></a
              >
            </li>
            <li class="nav-item">
              <a
                class="nav-link"
                :class="{ active: whichIs === '' }"
                href="#"
                @click.prevent="pickQ('')"
                ></a
              >
            </li>
            <li class="nav-item">
              <a
                class="nav-link"
                :class="{ active: whichIs === '' }"
                href="#"
                @click.prevent="pickQ('')"
                ></a
              >
            </li>
          </ul>
        </div>
        <div class="card-body p-0">
          <div class="accordion w-100 ml-auto">
            <div class="card" v-for="(item, index) in faqFilter" :key="index">
              <div class="card-header bg-white">
                <button
                  class="btn collapsed p-0"
                  type="button"
                  @click="toggleCollapse(item, index)"
                >
                  <ul class="list-unstyled">
                    <li>{{ "Q" + (index + 1) + ". " + item.question }}</li>
                  </ul>
                </button>
              </div>
              <div class="collapse" :class="{ show: item.show }" :key="index">
                <div class="card-body bg-secondary">
                  <ul class="list-unstyled">
                    <li>{{ "A" + (index + 1) + ". " + item.answer }}</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer></Footer>
    </div>
  </div>
</template>
<script>
import Navbar from "./Navbar";
import Footer from "./Footer";

export default {
  components: {
    Navbar,
    Footer
  },
  data() {
    return {
      whichIs: "",
      items: [
        {
          category: "",
          question: "",
          answer: "(VISAMastercardJCB)"
        },
        {
          category: "",
          question: "",
          answer: ""
        },
        {
          category: "",
          question: "",
          answer: ""
        },
        {
          category: "",
          question: "",
          answer: ""
        },
        {
          category: "",
          question: "",
          answer:
            ""
        },
        {
          category: "",
          question: " ",
          answer:
            "13:00 18:00 "
        }
      ]
    };
  },
  methods: {
    pickQ(Q) {
      const vm = this;
      vm.whichIs = Q;
    },
    toggleCollapse(item, index) {
      // show false/true
      if (item.show) {
        this.$set(item, "show", false);
      } else {
        this.$set(item, "show", true);
      }
    }
  },
  computed: {
    faqFilter() {
      const vm = this;
      let pay = [];
      let order = [];
      let send = [];
      if (vm.whichIs === "") {
        pay = vm.items.filter(function(item, index, array) {
          return item.category === "";
        });
        return pay;
      } else if (vm.whichIs === "") {
        order = vm.items.filter(function(item, index, array) {
          return item.category === "";
        });
        return order;
      } else if (vm.whichIs === "") {
        send = vm.items.filter(function(item, index, array) {
          return item.category === "";
        });
        return send;
      } else if (vm.whichIs === "") {
        return vm.items;
      }
    }
  }
};
</script>



